@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap);
html {
    overflow-x: hidden;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  background-color: white;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 24px;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    text-decoration: none;
    margin-left: 3.6px;
    margin-right: 3.6px;
}

img {
    max-width: 100%;
    overflow: auto;
    border-radius: 6px;
}

/* span, h1{
    text-decoration:none;
} */

h4 {
    font-weight: 700;
    text-shadow: 0 1px 0 black;
}

button:focus{
    outline:0px;
}
/*
button:hover {
    text-decoration:none;
} */

.header-bar {
    display: flex;
    padding: 0px 0px 0px 8px;
}

.links {
    position: absolute;
    right: 0;
}

@media screen and (max-width: 460px) {
    .links {
        position: relative;
    }
}

img {
    max-width: 100%;
    max-height: 500px;
    overflow: auto;
    border-radius: 6px;
}

.container {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    background-color: white;
    box-shadow: 0 1px 5px rgba(0,0,0,.05), 0 5px 30px rgba(0,0,0,.2);
    border-radius: 6px;
    padding: 24px;
    max-width: 100%;
    margin-bottom: 24px;
}

.img-size {
    max-width: 700px;
    max-height: 300px;
    display: flex;
}

.img-wrapper {
    right: 0;
    margin-bottom:24px;
}

.resume-container {
    position: relative;
}

.text-wrapper {
    /* position: absolute; */
    left: 0;
    flex: 1 1;
    margin-right: 24px;
}

@media screen and (max-width: 1020px) {
    .text-wrapper {
        flex: 100% 1;
        position: relative;
        margin: 0px 0px 24px 0px;
        max-width: 100%;
        flex-direction: column;
    }
    .img-wrapper {
        flex: 100% 1;
        flex-direction: column;
    }
    .img-size{
        width: 100%;
    }
}

.icon-container {
    padding: 24px 128px;
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 500px;
}

.center-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.bullet-wrapper {
    flex: 1 1;
    left: 0;
}

.project-img-wrapper {
    right: 0;
    margin-left: 24px;
}

.tabguard-size {
    width: 200px;
    height: 250px;
}

.predictor-size {
    width: 450px;
    height: 200px;
}

.c4me-size {
    width: 450px;
    height: 200px;
}

iframe.border {
    border-radius: 6px;
    overflow: hidden;
    min-width: 200px;
    min-height: 280px;
}

@media screen and (max-width: 450px) {
    iframe {
        max-width: 100%;
    }
}

.interests-container {
    position: relative;
    display: flex;
    margin-bottom: 24px;
    flex-wrap: wrap;
}

.interests-wrapper {
    /* background-color: #feefec; */
    box-shadow: 0 1px 5px rgba(0,0,0,.05), 0 5px 30px rgba(0,0,0,.2);
    border-radius: 6px;
    padding: 24px;
    flex: 22% 1;
}

.interests-wrapper.right {
    margin-right: 24px;
}

.carousel-size {
    max-width: 300px;
    height: 400px;
}

.media-wrapper {
    text-align: -webkit-center;
}
@media screen and (max-width: 1370px) {
    .interests-wrapper {
        flex: 45% 1;
        margin-bottom: 24px;
    }
}

@media screen and (max-width: 785px) {
    .interests-wrapper {
        flex-direction: column;
        flex: 100% 1;
        margin-bottom: 24px;
    }
    .interests-wrapper.right {
        margin-right: 0px;
    }
}

